import React, { useRef, useState } from "react";
import { CountdownCircleTimer } from 'react-countdown-circle-timer';
import {Button, Media} from 'react-bootstrap';
import './timer.css';




function Timer() {

  const width = window.innerWidth;
  const height = window.innerHeight;
  const[lenth,setLenth] = useState(Math.min(width,height)-20)

  const[isReset,setIsReset] = useState(true);
  const[isStart,setIsStart] = useState(false);
  const[isPlaying,setIsPlaying] = useState(false);
  const[key,setKey] = useState(0);
  const[seconds,setSeconds] = useState(10);

  window.addEventListener("resize",()=>{setLenth(Math.min(width,height)-20)})

  //버튼
  const 시작정지 = () => {
    setIsPlaying(!isPlaying);
    setIsStart(!isStart)
  }

  const 초기화 = () => {
    setIsReset(!isReset);
    setKey(key + 1);
    setIsPlaying(false);
    setIsStart(false);
  }

  const 입력 = () => {
    setIsReset(!isReset);
    setKey(key + 1)
  }

  
  


  return(
   
      
    <div className='ContainerStyle'>
      <div className='WrapperStyle'>
      <CountdownCircleTimer
        isPlaying={isPlaying}
        duration={parseInt(seconds)}
        key={key}
        size={lenth>740
        ? 900
      : 300}
        colors={[
          ['#004777', 0.33],
          ['#F7B801', 0.33],
          ['#A30000', 0.33],
        ]}
        
        >
           {({remainingTime}) => {
             if(remainingTime===0){
               return <div>time's up</div>
             } else {
               return (
                 <div>
              {isReset
              ? <div>
                <input onChange={(e)=>{setSeconds(e.target.value)}} ></input>
                <Button variant="outline-danger" onClick={() => {입력()}}>입력</Button>
              </div>
              : <div className='ClockStyle'>
                {remainingTime}
                <div className='BtnGroupStyle'>
                {isStart
                ?<Button variant="outline-danger" className='BtnStyle1' onClick={()=>{시작정지()}}>정지</Button>
              :<Button variant="outline-danger" className='BtnStyle1' onClick={()=>{시작정지()}}>시작</Button>}
                <Button variant="outline-danger" className='BtnStyle2' onClick={()=>{초기화()}} >초기화</Button>
                </div>
                </div> }
              </div>
               )}
           }}
          
        </CountdownCircleTimer>
        <Button variant="outline-danger" onClick={()=>console.log("seconds:" + seconds + "key:" + {key})}>test</Button>
        </div>
        </div>

    
  
  )}
export default Timer;